import React, { useEffect, useState } from 'react'
import { Card, Row, Col, CardPanel } from 'react-materialize'
import loginService from '../services/LoginService'
import versionResolver from '../utils/versionResolver'
import ToggleSwitch from './ToggleSwitch'

const Settings = (props) => {
  const [username, setUsername] = useState('Loading...')
  const [realname, setRealname] = useState('')
  const [tier, setTier] = useState('')
  const [email, setEmail] = useState('')
  const { darkmode } = props

  useEffect(() => {
    loginService.getUser().then(user => {
      setUsername(user.username)
      setRealname(user.realname)
      setTier(user.tier)
      setEmail(user.email)
    })
  })

  return (
    <div>
      <div className='container'>
        <Row>
          {`version ${versionResolver}`}
        </Row>
        <Row>
          <Card className="card blue-grey darken-1">
            <h3>Dark mode</h3>
            <ToggleSwitch
              isOn={darkmode.value}
              handleToggle={darkmode.onChange}
            />
          </Card>
        </Row>
        <Row>
          <Col s={12} m={5}>
            <CardPanel className='red accent-2 black-text'>
              <span>Currently can't change your information</span>
            </CardPanel>
          </Col>
          <Col s={12} m={7}>
            <CardPanel className='blue lighten-3 black-text'>
              <Row>
                <Col>Username: </Col>
                <Col>{username}</Col>
              </Row>
              <Row>
                <Col>Name: </Col>
                <Col>{realname}</Col>
              </Row>
              <Row>
                <Col>Level: </Col>
                <Col>{tier}</Col>
              </Row>
              <Row>
                <Col>Email: </Col>
                <Col>{email}</Col>
              </Row>
            </CardPanel>
          </Col>
        </Row>
      </div>
    </div>
  )
}

export default Settings
